import { useCallback, useState } from "react";

import { Employee } from "../types/api/Employee";

type Props = {
  id: string;
  employees: Array<Employee>;
  onOpen:() => void;
};

/**
 * 
 * @returns onSelectEmployee: selectedEmployee:
 */
export const useSelectEmployee = () => {
  const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null);
  const onSelectEmployee = useCallback((props: Props) => {
    const { id, employees, onOpen } = props;
    console.log(employees);
    console.log(id);
    const targetEmployee = employees.find((employee) => employee.employeeId === id);
    setSelectedEmployee(targetEmployee!);
    onOpen();
  }, []);

  return { onSelectEmployee, selectedEmployee };
};
