import axios from "axios";
import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useMessage } from "./useMessage";

type Props = {
  employeeId: string;
};

/**
 * 
 * @returns employeeDelete: loading:
 */
export const useEmployeeDelete = () => {
  const navigate = useNavigate();
  const { showMessage } = useMessage();
  const [loading, setLoading] = useState(false);

  const employeeDelete = useCallback(async (props: Props) => {
    const { employeeId } = props;
    setLoading(true);
    await axios
      .post("/api/delete", {
        employeeId: employeeId,
      })
      .then(() => {
        showMessage({ title: "", status: "success" });
        navigate("/home");
      })
      .catch(() => {
        showMessage({ title: "", status: "error" });
        setLoading(false);
      });
  }, []);

  return { employeeDelete, loading };
};
