import { useRecoilState } from "recoil";
import { useMessage } from "./useMessage";
import { useCallback, useState } from "react";
import { EmployeeState } from "../contexts/EmployeeState";
import axios from "axios";

/**
 * 
 * @returns editPassword: loading:
 */
export const useEditPassword = () => {
  const { showMessage } = useMessage();
  const [loading, setLoading] = useState(false);
  const [employeeState, setEmployeeState] = useRecoilState(EmployeeState);
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");

  const editPassword = useCallback(async () => {
    setLoading(true);
    await axios
      .post("/api/editPassword", {
        employeeId: employeeState,
        oldPassword: oldPassword,
        newPassword: newPassword,
      })
      .then(() => {
        showMessage({
          title: "",
          status: "success",
        });
        setLoading(false);
        setOldPassword("");
        setNewPassword("");
      })
      .catch(() => {
        showMessage({
          title:
            "",
          status: "error",
        });
        setLoading(false);
        setOldPassword("");
        setNewPassword("");
      });
  }, []);

  return { editPassword, loading };
};
