import axios from "axios";
import { useCallback, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useMessage } from "./useMessage";

type Props = {
  employeeId: string;
  password: string;
  isAdmin: string;
};

/**
 * 
 * @returns employeeRegister: loading:
 */
export const useEmployeeRegister = () => {
  const navigate = useNavigate();
  const { showMessage } = useMessage();
  const [loading, setLoading] = useState(false);

  const employeeRegister = useCallback(async (props: Props) => {
    const { employeeId, password, isAdmin } = props;
    setLoading(true);
    await axios
      .post("/api/register", {
        employeeId: employeeId,
        password: password,
        isAdmin: parseInt(isAdmin),
      })
      .then(() => {
        showMessage({ title: "", status: "success" });
        navigate("/home");
      })
      .catch(() => {
        showMessage({ title: "", status: "error" });
        setLoading(false);
      });
  }, []);

  return { employeeRegister, loading };
};
